/*

    Attempt to give all buttons the same height.
    - but don't really worry about baseline for now
    - define solid-color buttons w/ same-color borders

    Regular / Solid Button
    - solid color background

    Pill Button
    - similar to regular button but narrower with larger radius

    Outline Button
    - thick colored outline
    - text color usually same as outline
    - 'transparent' center (same color as bg)

    Text / Transparent Button
    - looks a lot like a link rather than button
    - transparent bg and border
    - on hover effects can give it more button-y looks
      - border or underline on hover
    - sometimes good to inherit button dimensins for baseline



    Colors & Styles
    - the standard buttons are black/white

*/
button.__shadow,
.__shadow._button {
  box-shadow: 3px 3px 6px rgba(51, 51, 51, 0.3); }

button.__shadow:hover,
.__shadow._button:hover {
  box-shadow: 3px 3px 6px rgba(51, 51, 51, 0.44); }

button,
a._button,
._button {
  cursor: pointer;
  outline: none;
  display: inline-block;
  line-height: 13px;
  margin-bottom: 16px;
  margin-right: 16px;
  background-color: transparent;
  border-width: 2px;
  border-style: solid;
  border-radius: 4px;
  border-color: transparent;
  padding: 15.5px 24px; }
  button:last-child,
  ._button:last-child {
    margin-right: 0; }
  
  button ._button.__active,
  ._button ._button.__active {
    border-width: 12px; }
  button.__disabled,
  .__disabled._button, button.__disabled:hover,
  .__disabled._button:hover, button[disabled],
  ._button[disabled], button[disabled]:hover,
  ._button[disabled]:hover {
    background-color: #888888 !important;
    color: #FAFAFA !important;
    border-color: #888888 !important; }
  button.__opacity-half,
  .__opacity-half._button {
    opacity: 0.5; }
    button.__opacity-half:hover,
    .__opacity-half._button:hover {
      opacity: 1; }
  button.__short,
  .__short._button {
    padding-top: 9.5px;
    padding-bottom: 9.5px;
    height: 32px; }
  @media (min-width: 768px) {
    button.__short-sm,
    .__short-sm._button {
      padding-top: 9.5px;
      padding-bottom: 9.5px;
      height: 32px; } }
  button.__thin,
  .__thin._button {
    line-height: 16px; }
  button.__thin-light,
  .__thin-light._button {
    line-height: 16px;
    border-width: 2px; }
  button.__tiny,
